import { StatusMessage } from './status-message';
import createComponent from './createComponent';

export default (type, message, params = {}) => {
    try {
        createComponent({
            componentType: params.componentType || StatusMessage,
            renderTo: 'main',
            renderMode: 'top',
            type,
            message,
            ...params,
        });
    } catch (e) {
        // eslint-disable-next-line no-alert
        alert(`${type} | ${message}`);
    }
};
