import { createElement, useState } from 'react';
import { PropTypes } from 'prop-types';
import classNames from 'classnames';
import { Translate, Alert, Button, Popover, Item, Icon } from '@plesk/ui-library';
import { api } from 'jsw';

const STATUS_DEFAULT = 'default';
const STATUS_TEST = 'test';
const STATUS_TRIAL = 'trial';
const STATUS_EXPIRED = 'expired';

const LicenseStatus = ({ status, daysLeft, isAdmin, actionUrl, installUrl, hidePopoverUrl, trialOverIcon, baseClassName }) => {
    const [trialOverPopoverVisible, setTrialOverPopoverVisible] = useState(daysLeft <= 5 && hidePopoverUrl);

    const handleClosePopover = () => {
        setTrialOverPopoverVisible(false);
        api.post(hidePopoverUrl);
    };

    const renderActions = () => {
        const alertCls = `${baseClassName}-alert`;

        return (actionUrl || installUrl) ? (
            <div className={`${alertCls}__actions`}>
                {actionUrl && (
                    <Button
                        intent="primary"
                        component="a"
                        icon="shopping-cart"
                        className={`${alertCls}__actions-button`}
                        href={actionUrl}
                        target="_blank"
                    >
                        <Translate content={status === STATUS_DEFAULT ? 'license-status.getTrialButton' : 'license-status.buyLicenseButton'} />
                    </Button>
                )}
                {installUrl && (
                    <a
                        className={`${alertCls}__actions-link`}
                        href={installUrl}
                    >
                        <Translate content="license-status.installLicenseButton" />
                    </a>
                )}
                {status === 'demo' && (
                    <Translate
                        content="license-status.demoLicenseTryPlesk"
                        className={`${alertCls}__additional-action`}
                        params={{
                            link: (
                                <a
                                    href="https://docs.plesk.com/try-plesk-now/"
                                    target="_blank"
                                    rel="nofollow noopener noreferrer"
                                >
                                    <Translate content="license-status.demoLicenseTryPleskLink" />
                                </a>
                            ),
                        }}
                    />
                )}
            </div>
        ) : null;
    };

    const renderTrialPopover = content => {
        const popoverCls = `${baseClassName}-popover`;

        return trialOverPopoverVisible ? (
            <Popover
                className={popoverCls}
                visible
                onClose={handleClosePopover}
                target={content}
            >
                <Item
                    title={<h4><Translate content="license-status.trialOverTitle" /></h4>}
                    icon={<Icon size="48" src={trialOverIcon} />}
                >
                    <Translate content="license-status.trialOverText" />
                </Item>
            </Popover>
        ) : content;
    };

    const alertCls = `${baseClassName}-alert`;

    let intent = null;
    if (status !== 'demo') {
        intent = daysLeft <= 0 ? 'danger' : (daysLeft <= 5 ? 'warning' : null);
    }

    const content = (
        <div
            className={classNames(baseClassName, { 'license-status--active': trialOverPopoverVisible })}
            data-type="license-status"
        >
            <Alert
                data-type={`license-status-${status}`}
                className={alertCls}
                intent={intent}
            >
                <div className={`${alertCls}__inner`}>
                    <div className={`${alertCls}__message`}>
                        {STATUS_TRIAL === status ? (
                            <div className={`${alertCls}__message-count`}>{daysLeft}</div>
                        ) : null}
                        <div className={`${alertCls}__message-text`}>
                            <Translate content={isAdmin || status === 'demo' ? `license-status.${status}LicenseWarningAdmin` : 'license-status.testLicenseWarningClient'} />
                        </div>
                    </div>
                    {renderActions()}
                </div>
            </Alert>
        </div>
    );

    return renderTrialPopover(content);
};

LicenseStatus.propTypes = {
    status: PropTypes.oneOf([STATUS_DEFAULT, STATUS_TEST, STATUS_TRIAL, STATUS_EXPIRED]).isRequired,
    daysLeft: PropTypes.number,
    isAdmin: PropTypes.bool.isRequired,
    actionUrl: PropTypes.string,
    installUrl: PropTypes.string,
    hidePopoverUrl: PropTypes.string,
    trialOverIcon: PropTypes.string,
    baseClassName: PropTypes.string,
};

LicenseStatus.defaultProps = {
    daysLeft: 0,
    actionUrl: null,
    installUrl: null,
    hidePopoverUrl: null,
    trialOverIcon: null,
    baseClassName: 'license-status',
};

export default LicenseStatus;
