import { Text, Button, Translate } from '@plesk/ui-library';
import { createElement, useEffect, Fragment } from 'react';
import PropTypes from 'prop-types';
import { UAT } from 'jsw';
import { NotificationPropType } from './lib/PropTypes';

const NotificationToast = ({
    notification,
    onSelectNotification,
}) => {
    const { subject, context, code, createdAt } = notification;

    useEffect(() => {
        UAT.dispatchAction('NOTIFICATION_TOAST_SHOW', {
            createdAt,
            type: code,
        });

        return () => {
            UAT.dispatchAction('NOTIFICATION_TOAST_HIDE', {
                type: code,
            });
        };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <Fragment>
            <Text>{context}</Text>
            <br />
            <Text bold>{subject}</Text>
            <br />
            <Button
                data-action="NOTIFICATION_DETAILS"
                data-type={code}
                ghost
                onClick={() => onSelectNotification({ notification })}
            >
                <Translate content="notification-center.learnMore" />
            </Button>
        </Fragment>
    );
};

NotificationToast.propTypes = {
    notification: NotificationPropType.isRequired,
    onSelectNotification: PropTypes.func.isRequired,
};

export default NotificationToast;
