import { api } from 'jsw';

export const listNotifications = id => api.get('/admin/notifications/list', { 'filter[older]': id });

export const updateNotifications = (ids, change) => api.post('/admin/notifications/update', { ids, ...change });

export const deleteNotification = id => api.post('/admin/notifications/delete', { id });
