import { createElement, useMemo } from 'react';
import PropTypes from 'prop-types';
import { Heading, Translate, Paragraph, Link, FormFieldRadioButtons } from '@plesk/ui-library';
import DrawerAdapter from './DrawerAdapter';
import { PLESK_COOKIES, CONTROL_COOKIES } from '../urls';

const toValues = preferences => Object.keys(preferences).reduce((acc, k) => {
    acc[k] = preferences[k] ? 'on' : 'off';
    return acc;
}, {});

const toPreferences = values => Object.keys(values).reduce((acc, k) => {
    acc[k] = values[k] === 'on';
    return acc;
}, {});

const Preferences = ({ isOpen, onSave, onToggle, preferences, adapter: Adapter = DrawerAdapter }) => {
    const namespace = 'components.cookie-policy.preferences';
    const values = useMemo(() => toValues(preferences), [preferences]);

    const handleSubmit = values => {
        onSave(toPreferences(values));
        onToggle(false);
    };

    const handleCancel = () => onToggle(false);

    return (
        <Adapter
            isOpen={isOpen}
            title={<Translate namespace={namespace} content="title" />}
            submitButton={{
                children: (
                    <Translate
                        namespace={namespace}
                        content="submitButton"
                        data-type="cookie-policy-preferences-submit-button"
                    />
                ),
            }}
            cancelButton={{
                children: (
                    <Translate
                        namespace={namespace}
                        content="cancelButton"
                        data-type="cookie-policy-preferences-cancel-button"
                    />
                ),
                onClick: handleCancel,
            }}
            values={values}
            onSubmit={handleSubmit}
            onCancel={handleCancel}
        >
            <Paragraph>
                <Translate namespace={namespace} content="description" />
            </Paragraph>
            <Heading level={2}>
                <Translate namespace={namespace} content="settingsTitle" />
            </Heading>
            <Paragraph>
                <Translate namespace={namespace} content="settingsDescription" />
            </Paragraph>
            <Heading level={3}>
                <Translate namespace={namespace} content="analyticsTitle" />
            </Heading>
            <Paragraph>
                <Translate namespace={namespace} content="analyticsDescription" />
            </Paragraph>
            <FormFieldRadioButtons
                name="analytics"
                value={values.analytics}
                className="cookie-policy-preferences__radio-buttons"
                options={[
                    { value: 'on', label: <Translate namespace={namespace} content="on" /> },
                    { value: 'off', label: <Translate namespace={namespace} content="off" /> },
                ]}
            />
            <Heading level={3}>
                <Translate namespace={namespace} content="marketingTitle" />
            </Heading>
            <Paragraph>
                <Translate namespace={namespace} content="marketingDescription" />
            </Paragraph>
            <FormFieldRadioButtons
                name="marketing"
                value={values.marketing}
                className="cookie-policy-preferences__radio-buttons"
                options={[
                    { value: 'on', label: <Translate namespace={namespace} content="on" /> },
                    { value: 'off', label: <Translate namespace={namespace} content="off" /> },
                ]}
            />
            <Heading level={3}>
                <Translate namespace={namespace} content="necessaryTitle" />
            </Heading>
            <Paragraph>
                <Translate
                    namespace={namespace}
                    content="necessaryDescription"
                    params={{
                        browserSettingsLink: (
                            <Link href={CONTROL_COOKIES} target="_blank">
                                <Translate namespace={namespace} content="browserSettingsLink" />
                            </Link>
                        ),
                    }}
                />
            </Paragraph>
            <Paragraph>
                <Link href={PLESK_COOKIES} target="_blank">
                    <Translate namespace={namespace} content="pleskCookiesLink" />
                </Link>
            </Paragraph>
        </Adapter>
    );
};

Preferences.propTypes = {
    isOpen: PropTypes.bool.isRequired,
    onSave: PropTypes.func.isRequired,
    onToggle: PropTypes.func.isRequired,
    preferences: PropTypes.object.isRequired,
    adapter: PropTypes.func,
};

Preferences.defaultProps = {
    adapter: undefined,

};

export default Preferences;
