import { Badge, Button, Dropdown, Menu, MenuDivider, MenuItem, Translate } from '@plesk/ui-library';
import { createElement } from 'react';
import PropTypes from 'prop-types';
import { NotificationFilterPropType } from '../lib/PropTypes';

const FilterMenu = ({ filter, filterCount, onUpdateFilter }) => {
    const menu = (
        <Menu>
            <MenuItem
                icon={filter && filter.isUnRead ? 'check-mark' : null}
                onClick={() => onUpdateFilter({ isUnRead: !filter.isUnRead, isCritical: false, isFavorite: false })}
            >
                <Translate content="notification-center.filterUnread" />
            </MenuItem>
            <MenuItem
                icon={filter && filter.isCritical ? 'check-mark' : null}
                onClick={() => onUpdateFilter({ isUnRead: false, isCritical: !filter.isCritical, isFavorite: false })}
            >
                <Translate content="notification-center.filterCritical" />
            </MenuItem>
            <MenuItem
                icon={filter && filter.isFavorite ? 'check-mark' : null}
                onClick={() => onUpdateFilter({ isUnRead: false, isCritical: false, isFavorite: !filter.isFavorite })}
            >
                <Translate content="notification-center.filterFavorite" />
            </MenuItem>
            <MenuDivider />
            <MenuItem
                icon="remove"
                onClick={() => onUpdateFilter({
                    search: '',
                    isUnRead: false,
                    isCritical: false,
                    isFavorite: false,
                })}
            >
                <Translate content="notification-center.filterReset" />
            </MenuItem>
        </Menu>
    );

    return (
        <Badge
            intent="info"
            hidden={filterCount === 0}
        >
            <Dropdown menu={menu}>
                <Button icon="filter">
                    <Translate content="notification-center.filterButton" />
                </Button>
            </Dropdown>
        </Badge>
    );
};

FilterMenu.propTypes = {
    filter: NotificationFilterPropType.isRequired,
    filterCount: PropTypes.number.isRequired,
    onUpdateFilter: PropTypes.func.isRequired,
};

export default FilterMenu;
